<template>
  <div class="main">
    <header>
      <div>
        <label for="row">
          <span>Row:</span>
          <input
            v-on:change="setRow"
            type="number"
            id="row"
            max="100"
            min="5"
            value="10"
            ref="row"
            onkeydown="return false"
          />
        </label>
        <label for="column">
          <span>Column:</span>
          <input
            v-on:change="setColumn"
            type="number"
            id="column"
            max="100"
            min="5"
            value="10"
            ref="column"
            onkeydown="return false"
          />
        </label>
        <label for="column">
          <button @click="onPlay">{{this.play ? "Stop" : "Play"}}</button>
          <button @click="clear">Clear</button>
        </label>
      </div>
      <div class="indicator">
        <div v-bind:class="{value: true, play: play }"></div>
      </div>
    </header>
    <br />
    <br />
    <div class="field" v-on:click="click" ref="el">
      <FieldRow
        v-bind:row="item"
        v-for="(item, index) in items"
        v-bind:key="index"
        v-bind:elementIndex="index"
      />
      <!-- <div v-for="(item, index) in items" v-bind:key="index">{{item}}</div> -->
    </div>
  </div>
</template>

<script>
/* eslint-disable no-unused-vars */
/* eslint-disable no-empty */

import FieldRow from "./FieldRow";

const itemsTo = Array(10).fill(Array(10).fill(false));

export default {
  name: "Field",
  mounted: function() {},
  methods: {
    clear: function() {
      let arrayLength = +this.$refs.row.value;
      let arrayColumnLength = +this.$refs.column.value;

      for (let index = 0; index < arrayLength; index++) {
        this.items[index] = Array(arrayColumnLength).fill(false);
        this.items = [...this.items];
      }
    },
    checValueItem: function(newItem, rowIndex, columnIndex) {
      if (newItem) {
        let idResult = 0;
        for (let indexRow = -1; indexRow < 2; indexRow++) {
          for (let indexColumn = -1; indexColumn < 2; indexColumn++) {
            let neiborItem;

            if (this.items[rowIndex + indexRow]) {
              if (this.items[rowIndex + indexRow][columnIndex + indexColumn]) {
                neiborItem = this.items[rowIndex + indexRow][
                  columnIndex + indexColumn
                ];
              } else {
                neiborItem = false;
              }
            } else {
              neiborItem = false;
            }

            if (indexRow === 0 && indexColumn === 0) {
              neiborItem = false;
            }

            if (neiborItem) {
              idResult += 1;
            }
          }
        }
        if (idResult === 2 || idResult === 3) {
          return true;
        }
        return false;
      } else {
        let idResult = 0;
        for (let indexRow = -1; indexRow < 2; indexRow++) {
          for (let indexColumn = -1; indexColumn < 2; indexColumn++) {
            let neiborItem;

            if (this.items[rowIndex + indexRow]) {
              if (this.items[rowIndex + indexRow][columnIndex + indexColumn]) {
                neiborItem = this.items[rowIndex + indexRow][
                  columnIndex + indexColumn
                ];
              } else {
                neiborItem = false;
              }
            } else {
              neiborItem = false;
            }

            if (indexRow === 0 && indexColumn === 0) {
              neiborItem = false;
            }

            if (neiborItem) {
              idResult += 1;
            }
          }
        }
        if (idResult === 3) {
          return true;
        }
        return false;
      }
    },
    fieldUpdate: function() {
      let arrayRowLength = +this.$refs.row.value;
      let arrayColumnLength = +this.$refs.column.value;
      let newArray = JSON.parse(JSON.stringify(this.items));
      for (let rowIndex = 0; rowIndex < arrayRowLength; rowIndex++) {
        for (
          let columnIndex = 0;
          columnIndex < arrayColumnLength;
          columnIndex++
        ) {
          let newItem = this.items[rowIndex][columnIndex];
          newItem = this.checValueItem(newItem, rowIndex, columnIndex);
          newArray[rowIndex][columnIndex] = newItem;
          // console.log(newArray)
        }
      }

      for (let index = 0; index < newArray.length; index++) {
        this.$set(this.items, [index], [...newArray[index]]);
      }
    },
    onPlay: function() {
      this.play = !this.play;
      if (this.play) {
        console.log("play");
        this.timerId = setInterval(this.fieldUpdate, 1000);
      } else {
        console.log("stop");
        clearInterval(this.timerId);
      }
    },
    setRow: function() {
      let arrayLength = +this.$refs.column.value;
      this.items.push(Array(arrayLength).fill(false));
    },
    setColumn: function() {
      let arrayLength = +this.$refs.row.value;
      let arrayColumnLength = +this.$refs.column.value;

      for (let index = 0; index < arrayLength; index++) {
        this.items[index] = Array(arrayColumnLength).fill(false);
        this.items = [...this.items];
      }
    },
    click: function(e) {
      this.moved = true;
      const position = this.getPosition(e);
      const itemIndex = this.getItemIndex(position);
      this.setItemValue(itemIndex, !this.items[itemIndex.y][itemIndex.x]);
    },

    setItemValue: function(itemIndex, value) {
      let arrayRow = this.items[itemIndex.y];
      arrayRow[itemIndex.x] = value;
      this.$set(this.items, [itemIndex.y], [...arrayRow]);
      arrayRow[itemIndex.x] = false;
    },
    getPosition(e) {
      var elementId = this.$refs.el;
      var rect = elementId.getBoundingClientRect();

      var x = e.clientX - rect.left;
      var y = e.clientY - rect.top;
      return {
        x,
        y
      };
    },
    getItemIndex(itemPosition) {
      return {
        x: Math.ceil(itemPosition.x / this.itemSize) - 1,
        y: Math.ceil(itemPosition.y / this.itemSize) - 1
      };
    }
  },
  components: {
    FieldRow
  },
  data: () => {
    return {
      items: itemsTo,
      itemSize: 30,
      play: false,
      timerId: null
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.field {
  border: 1px solid black;
}
.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.indicator {
  margin-top: 10px;
  width: 100%;
  height: 10px;
  display: flex;
  justify-content: flex-end;
}

.value {
  height: 100%;
  width: 100%;
  background: chartreuse;
}
.play {
  animation: box 1s infinite;
}

@keyframes box {
  from {
    width: 100%;
  }
  to {
    width: 0%;
  }
}
</style>
